.icon {
  display: inline-flex;
  pointer-events: none;
  user-select: none;
  transition: all 0.2s;

  svg {
    width: inherit;
    height: inherit;
  }

  path {
    transition: fill 0.2s;
  }

  svg * {
    // transition: all 0.2s;
  }
}

@mixin btn-hover {
  @at-root .btn:hover & {
    @content;
  }
}

@mixin btn-active {
  @at-root .btn:active & {
    @content;
  }
}

@mixin btn-state($state) {
  @at-root .btn[#{$state}] & {
    @content;
  }
}

// ----------------------------------------------------------------------------------------

.icon-drawer {
  background-color: transparent;
  border-radius: 1rem;
  border: solid 1px transparent;

  rect {
    transition: all 0.3s;
  }

  @include btn-hover {
    background-color: $white102;
  }

  @include btn-active {
    background-color: $white103;
    border-color: $white103;
  }

  @include btn-state('aria-expanded="true"') {
    background-color: transparent;
    .icon-drawer {
      .rect00 {
        transform: translate(-2px, -2px);
      }
      .rect10 {
        transform: translate(-2px, 0px);
      }
      .rect20 {
        transform: translate(-2px, 2px);
      }
      .rect01 {
        transform: translate(0, -2px);
      }
      .rect11 {
        transform: translate(0, 0px);
      }
      .rect21 {
        transform: translate(0, 2px);
      }
      .rect02 {
        transform: translate(2px, -2px);
      }
      .rect12 {
        transform: translate(2px, 0);
      }
      .rect22 {
        transform: translate(2px, 2px);
      }
    }
  }
}

.icon-like {
  path {
    fill: transparent;
  }

  @include btn-hover {
    path {
      fill: $white101;
    }
  }

  @include btn-active {
    transform: scale(0.9);
    path {
      fill: $white108;
    }
  }

  @include btn-state('data-toggle=true') {
    path {
      fill: $day;
      stroke: $day;
    }

    animation-name: tada, shaking;
    animation-duration: 0.1s, 0.1s;
    animation-delay: 0s, 0.2s;
    animation-timing-function: ease-out, ease-in-out;
    animation-iteration-count: 1, 3;
  }
}

.icon-theme {
  .svg-day-night {
    * {
      transition: all 0.6s;
    }
    .sun-light {
      transform-origin: center;
      transition: all 0.4s;
    }
    .bg-light {
      stroke: $white;
      stroke-width: 1px;
    }
  }

  @include theme-light {
    .bg-light {
      stroke-opacity: 0;
    }
  }

  @include theme-dark {
    .eclipse {
      mask: url(#eclipse);
    }
    .sun-light {
      transform: scale(0);
    }
    .bg-light {
      stroke-opacity: 0.2;
    }
  }
}

.icon-tree-index {
  .rect {
    // transition: height 0.1s;
  }

  // spread
  @include btn-state('aria-expanded=true') {
    .rect {
      height: 10px;
    }
  }
  // folded
  @include btn-state('aria-expanded=false') {
    .rect {
      transform: translateY(4px);
      height: 2px;
    }
  }
}
